import React, { createElement } from 'react'
import { Button } from '../components'
import rehypeReact from 'rehype-react'
import styled from 'styled-components'
import GithubReactionButton from 'github-reaction-button'

const renderAst = new rehypeReact({
  createElement,
  components: {
    button: Button
  }
}).Compiler

const Header = styled.div`
`
const Reaction = new GithubReactionButton({
  client_id: 'fa316209fdb16b865f7d',
  client_secret: 'e08f6d652b46ae5c7bc212f8ad8fcd14e1cc29bb',
  redirect_uri: 'http://localhost:4000',
  repo: 'yusukeshibata.github.io',
  owner: 'yusukeshibata',
  issue: 1
})

export default ({ data }) => {
  if(!data || !data.markdownRemark) return false
  const { markdownRemark: { excerpt, htmlAst, frontmatter } } = data
  return (
    <div>
      <Header>
        <h1>{frontmatter.title ? `${frontmatter.title}  ` : ''}{frontmatter.date}</h1>
      </Header>
      { renderAst(htmlAst) }
      <Reaction.component id={frontmatter.id} content={excerpt} />
    </div>
  )
}
export const query = graphql`
  query PostQuery($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: {slug: {eq: $slug}}) {
      excerpt(pruneLength:1024)
      htmlAst
      frontmatter {
        title
        subtitle
        id: date(formatString: "YYYYMMDD")
        date(formatString: "DD MMMM, YYYY")
      }
    }
  }
`



